import React, { Component } from 'react';
import { Timeline } from 'antd';
import { ClockCircleOutlined } from '@ant-design/icons';
import "./experience.less";
import cs from "classnames"
import PropTypes from 'prop-types';
import bbg from "./images/land.png"


class Experience extends Component {

    render() {
        let {setAnimate} = this.props;
        return (
            <div className="experience">
                {/* <div className="page-tit">
                    <div className="tit">我的经历</div>
                </div> */}
                <div className="timepart">
                    <div className="now">
                        <div className={cs('time','animated', { 'fadeIn': setAnimate, })}>2020</div>
                    </div>
                    <Timeline className={cs('mytimeline','animated', { 'fadeIn': setAnimate, })} mode="alternate" pending={true} pendingDot={<div></div>}>
                        <Timeline.Item label={<div className="activetime">2018-</div>} dot={<ClockCircleOutlined style={{ fontSize: '38px',background:'#fff',borderRadius:'50%'}} />} color="green">
                            <div className="timeLineTxt activetxt">南通某科技公司</div>
                            </Timeline.Item>
                        <Timeline.Item label="2016-07-01"><div className="timeLineTxt">同程网络科技股份有限公司</div></Timeline.Item>
                        <Timeline.Item label="2014-07-01"><div className="timeLineTxt">在上海工作</div></Timeline.Item>
                    </Timeline>
                    <div className={cs('moon','animated', { 'moonFloat': setAnimate})}></div>
                    <div className={cs('bottomBg','animated', { 'fadeIn': setAnimate, })}>
                        <img src={bbg}/>
                    </div>

                </div>
            </div>
        )
    }
}

Experience.defaultProps = {
    setAnimate: false
  };

Experience.propTypes = {
    setAnimate:PropTypes.bool,
}

export default Experience;