import React, { Component } from 'react';
import './loading.less';
import PropTypes from 'prop-types';
import cs from "classnames"

import star from "./images/star.png"
import star6 from "./images/star6.png"

class Loading extends Component {


    render() {
        let {ifloading} = this.props;
        return (
            <div className={cs('loading-page', 'animated',{ 'loadingfadeOut': !ifloading })}>
                <div className="loading-show">
                    <img className="star" src={star}/>
                    <div className="loading-nums">
                        <img className="star6" src={star6}/>
                        <img className="star6" src={star6}/>
                        <img className="star6" src={star6}/>
                        <img className="star6" src={star6}/>
                        <img className="star6" src={star6}/>
                    </div>
                </div>
            </div>
        )
    }
}

Loading.defaultProps = {
    ifloading: true
  };

Loading.propTypes = {
    ifloading:PropTypes.bool,
}

export default Loading;
