import React, { Component } from 'react'
import "./skill.less"

import echarts from "echarts/lib/echarts";
import "echarts/lib/chart/radar";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
import cs from "classnames"
import PropTypes from 'prop-types';

class Skill extends Component {
    constructor(props) {
        super(props);
        this.option = {
            radar: {
                shape: 'circle',
                name: {
                    formatter: function (value, indicator) {
                        var color = "green";
                        var val = indicator.val;
                        return '{a|' + value + ' }{' + color + '|' + val + '%}'
                    },
                    rich: {
                        a: {
                            color: '#ffca28',
                            fontSize: 26
                        },
                        green: {
                            fontSize: 22,
                            color: '#F2E686',
                            fontWeight: 'bold'
                        }
                    },
                    textStyle: {
                        color: '#4B4B71',
                    }
                },
                indicator: [
                    { name: 'Html5', max: 100, val: 90 },
                    { name: 'css3', max: 100, val: 90 },
                    { name: 'JQuery ', max: 100, val: 85 },
                    { name: 'vue', max: 100, val: 75 },
                    { name: 'react', max: 100, val: 65 },
                    { name: 'JavaScript', max: 100, val: 80 }
                ],

                splitArea: {
                    areaStyle: {
                        color: '#FFFDE7',
                        shadowColor: 'rgba(219, 246, 254, 1)',
                        shadowBlur: 16
                    }
                },
                axisLine: {
                    lineStyle: {
                        color: '#e0e0e0'
                    }
                },
                splitLine: {
                    lineStyle: {
                        color: '#e0e0e0'
                    }
                }
            },
            series: [{
                type: 'radar',
                data: [
                    {
                        value: [90, 90, 85, 75, 65, 80],
                        lineStyle: {
                            color: '#556483',
                            width: 2
                        },
                        areaStyle: {
                            color: 'rgba(0, 49, 113,0.5)'
                        },
                        symbolSize: 12,

                        itemStyle: {
                            color: "#556483"
                        },
                        emphasis: {
                            areaStyle: {
                                color: 'rgba(0, 49, 113,0.9)'
                            },
                        },
                    }
                ]
            }]
        }
    }

    componentDidMount() {
        var myChart = echarts.init(document.getElementById("skilldom"));
        myChart.setOption(this.option)
    }


    render() {
        let {setAnimate} = this.props;
        return (
            <div className="skill">
                <div id="skilldom" className={cs('chart-part','animated', { 'rollIn': setAnimate })} >

                </div>
            </div>
        )
    }
}

Skill.defaultProps = {
    setAnimate: false
  };

Skill.propTypes = {
    setAnimate:PropTypes.bool,
}

export default Skill;