import React, { Component } from 'react'
import cs from "classnames"
import PropTypes from 'prop-types';
import {
    MailFilled,
} from '@ant-design/icons';
import "./contact.less"

import leftlianpic from "./images/leftlian.png"
import rightlianpic from "./images/rightlian.png"
class ContactMe extends Component{
    render() {
        let {setAnimate} = this.props;
        return (
            <div className="contact-me">
               <div className={cs('leftlian','animated', { 'fadeInLeft': setAnimate, })} >
                   <img src={leftlianpic}/>
               </div>
               <div className={cs('rightlian','animated', { 'fadeInRight': setAnimate, })}>
                    <img src={rightlianpic}/>
               </div>
               <div className={cs('moon','animated', { 'moonFloat': setAnimate, })}></div>
               <div className={cs('stars','animated', { 'fadeIn': setAnimate, })}></div>
               <div className={cs('email-content','animated', { 'fadeIn': setAnimate, })}>
                  <MailFilled className="emailicon animated tada"/>
                  <span className="email-num">947959277@qq.com</span>
               </div>
            </div> 
        )
    }
}

ContactMe.defaultProps = {
    setAnimate: false
};

ContactMe.propTypes = {
    setAnimate:PropTypes.bool,
}

export default ContactMe;