import React, { Component } from 'react'
import $ from 'jquery';
import Swiper from 'swiper/dist/js/swiper.js';
import 'swiper/dist/css/swiper.min.css';
// import { TweenMax, Power4 } from 'gsap';
import { CSSTransition, TransitionGroup } from 'react-transition-group'
import Parallax from 'parallax-js'

import './introduction.less'
import bg1 from './images/topslide1.jpg';
import bg2 from './images/topslide2.jpg';
import bg3 from './images/topslide3.jpg';
import bg4 from './images/topslide4.jpg';

// import { deepcopy } from '../../common/js/util.js'

class Introduction extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pieces: 5,
            canvasList: [],
        }
        this.imgw = 1920;
        this.imgh = 1080;
        this.tweenObj = {
            translate: 0
        };
        this.slidesLength = 3;
        this.clientWidth = document.body.clientWidth;
        this.clientHeight = document.body.clientHeight;
        // 每一个显示块的宽度
        this.blockWidth = this.clientWidth / this.state.pieces;
        this.activeIndex = 0;
    }

    componentDidMount() {
        this.initSwiper();

    }
    initSwiper() {
        let that = this;
        this.introSwiper = new Swiper('.myintro-swiper-container', {
            effect : 'fade',
            speed: 3000,
            autoplay: 10000,
            autoplayDisableOnInteraction:false,
            onInit:function(){
                console.log("init")
                let scene = document.getElementById('scene');
                let parallaxInstance = new Parallax(scene);
            },
            onSlideChangeStart: function (swiper) {
                that.activeIndex = swiper.activeIndex;
                that.canvasEnter();
                let dom = document.getElementById("shows0");
                let arr = document.querySelectorAll(".mytopimg");
                if(dom){
                    that.resize(swiper);
                    let deg = 90;
                    let degtime = setInterval(() => {
                        deg = deg-1.8;
                        that.canvas1.style.transform = `rotateY(${deg}deg)`;
                        that.canvas2.style.transform = `rotateY(${deg}deg)`;
                        that.canvas3.style.transform = `rotateY(${deg}deg)`;
                        that.canvas4.style.transform = `rotateY(${deg}deg)`;
                        that.canvas5.style.transform = `rotateY(${deg}deg)`;
                        if(deg>75 && deg < 90){
                            arr[swiper.activeIndex].style.opacity = 0.4;

                        }else if(deg>60 && deg < 75){
                            if(arr[swiper.activeIndex].style.opacity !== 0.5){
                                arr[swiper.activeIndex].style.opacity = 0.5;
                            }
                        }else if(deg>45 && deg < 60){
                            if(arr[swiper.activeIndex].style.opacity !== 0.6){
                                arr[swiper.activeIndex].style.opacity = 0.6;
                            }
                        }else if(deg>30 && deg < 45){
                            if(arr[swiper.activeIndex].style.opacity !== 0.7){
                                arr[swiper.activeIndex].style.opacity = 0.7;
                            }
                             
                        }else if(deg>15 && deg < 30){
                            if(arr[swiper.activeIndex].style.opacity !== 0.8){
                                arr[swiper.activeIndex].style.opacity = 0.8;
                            }
                            
                        }else if(deg>0 && deg < 15){
                            if(arr[swiper.activeIndex].style.opacity !== 0.9){
                                arr[swiper.activeIndex].style.opacity = 0.9;
                            }
                            
                        }

                        if(deg === 0 || deg < 0){
                            
                            arr[swiper.activeIndex].style.opacity = 1;
                            clearInterval(degtime)
                        }
                    }, 50);
                }
            },
            onSlideChangeEnd:function(){
                that.canvasLeave();
            }
        });
    }

    //剪裁画布
    resize(swiper) {
        let { pieces } = this.state;
        let that = this;
        let currentImage = $(".mytopimg").eq(swiper.activeIndex)[0];

        //创建画布
        this.canvas1 = document.getElementById("shows0");
        this.canvas2 = document.getElementById("shows1");
        this.canvas3 = document.getElementById("shows2");
        this.canvas4 = document.getElementById("shows3");
        this.canvas5 = document.getElementById("shows4");
        this.ctx1 = this.canvas1.getContext("2d");
        this.ctx2 = this.canvas2.getContext("2d");
        this.ctx3 = this.canvas3.getContext("2d");
        this.ctx4 = this.canvas4.getContext("2d");
        this.ctx5 = this.canvas5.getContext("2d");

        let p = this.imgw / pieces;
        console.log(this.blockWidth,p)

        this.ctx1.drawImage(
            currentImage,
            0,
            0,
            p,
            this.imgh * 4,
            0,  //建材后的距离
            0,
            p,   //剪裁后的宽度
            that.clientHeight,
        );
        this.ctx2.drawImage(
            currentImage,
            p,
            0,
            p,
            this.imgh * 4,
            0,  //建材后的距离
            0,
            p,   //剪裁后的宽度
            that.clientHeight,
        );
        this.ctx3.drawImage(
            currentImage,
            p * 2,
            0,
            p,
            this.imgh * 4,
            0,  //建材后的距离
            0,
            p,   //剪裁后的宽度
            that.clientHeight,
        );
        this.ctx4.drawImage(
            currentImage,
            p * 3,
            0,
            p,
            this.imgh * 4,
            0,  //建材后的距离
            0,
            p,   //剪裁后的宽度
            that.clientHeight,
        );
        this.ctx5.drawImage(
            currentImage,
            p * 4,
            0,
            p,
            this.imgh * 4,
            0,  //建材后的距离
            0,
            p,   //剪裁后的宽度
            that.clientHeight,
        );

    }

    canvasEnter() {
        let { pieces } = this.state;
        this.clientWidth = document.body.clientWidth;
        this.clientHeight = document.body.clientHeight;
        // 每一个显示块的宽度
        this.blockWidth = this.clientWidth / pieces;
        let arr = document.querySelectorAll(".mytopimg");
        for(let v of arr){
            v.style.opacity = 0;
        }
        arr[this.activeIndex].style.opacity = 0.3;

        this.setState({
            canvasList: [0, 1, 2, 3, 4]
        })
    }

    canvasLeave(){
        this.setState({
            canvasList: [],
        })
    }

    render() {
        let { canvasList } = this.state;
        return (
            <div className="myintro">
                <div className="myintro-swiper-container swiper-container">
                    <div className="swiper-wrapper">
                        <div className="swiper-slide" >
                            <img alt="" src={bg1} className="mytopimg" />
                        </div>
                        <div className="swiper-slide">
                            <img alt="" src={bg2} className="mytopimg" />
                        </div>
                        <div className="swiper-slide" >
                            <img alt="" src={bg3} className="mytopimg" />
                        </div>
                        <div className="swiper-slide" >
                            <img alt="" src={bg4} className="mytopimg" />
                        </div>
                    </div>
                </div>
                <TransitionGroup>
                    {
                        canvasList.map((i) => {
                            return (
                                <CSSTransition
                                    key={i}
                                    timeout={1000}
                                    classNames='fade'
                                ><canvas id={'shows' + i} className="cover" style={{
                                    width: this.blockWidth + "px",
                                    height: this.clientHeight + "px",
                                    left: this.blockWidth * i + "px",
                                }}></canvas></CSSTransition>
                            )
                        })
                    }
                </TransitionGroup>
                <div className="into-part">
                    <div className="myself" id="scene">
                        <div className="hi" data-depth="0.2">Hello~</div>
                        <h2 className="sayhi" data-depth="0.4">I'm Wendy</h2>
                        <div className="job" data-depth="0.6">Front-end Engineer  | 前端工程师</div>
                    </div>
                    <div className="arrowdwon animated fadeIn"></div>
                </div>
            </div>
        )
    }
}

export default Introduction;