import React, { Component } from 'react';
import { Row, Col, Card } from 'antd';
import "./project.less";
import cs from "classnames"
import PropTypes from 'prop-types';

import bg1 from "./images/p1.png";
import bg2 from "./images/p2.jpg";
import bg3 from "./images/p3.png";
import bg4 from "./images/p4.png";

class Project extends Component {
    constructor(props) {
        super(props);

    }

    componentDidMount() {

    }


    render() {
        let {setAnimate} = this.props;
        return (
            <div className="project">
                <div className="page-tit">
                    <div className="tit">我的项目</div>
                </div>
                <div className="show-project">
                    <Row gutter={[16, 16]}>
                        <Col xs={12} sm={12} md={6} lg={6} xl={6}>
                            <Card
                                hoverable
                                className={cs('animated', { 'rollIn': setAnimate })}
                            >
                                <div className="card-box">
                                    <div className="img-box">
                                        <img className="bg bg1" src={bg1}/>
                                    </div>
                                    <div className="mask animated fadeInUp">
                                        <div className="content">
                                            <div className="txt animated fadeInDown">
                                                <div className="tit">PC</div>
                                                <div className="detail">运用html+css+jq/vue/react iview/antDesign 开发系统管理页面，商城，活动页面</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Card>
                        </Col>
                        <Col xs={12} sm={12} md={6} lg={6} xl={6}>
                            <Card
                                hoverable
                                className={cs('animated', { 'rollIn': setAnimate })}
                            >
                                <div className="card-box">
                                    <div className="img-box">
                                        <img className="bg bg3" src={bg3}/>
                                    </div>
                                    <div className="mask animated fadeInUp">
                                        <div className="content">
                                            <div className="txt animated fadeInDown">
                                                <div className="tit">移动端页面</div>
                                                <div className="detail">
                                                    运用vue/vant/weiui 开发app内嵌、移动端页面
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Card>
                        </Col>
                        <Col xs={12} sm={12} md={6} lg={6} xl={6}>
                            <Card
                                hoverable
                                className={cs('animated', { 'rollIn': setAnimate })}
                            >
                                <div className="card-box">
                                    <div className="img-box">
                                        <img className="bg bg2" src={bg2}/>
                                    </div>
                                    <div className="mask animated fadeInUp">
                                        <div className="content">
                                            <div className="txt animated fadeInDown">
                                                <div className="tit">小程序</div>
                                                <div className="detail">
                                                    商城类小程序
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Card>
                        </Col>
                        <Col xs={12} sm={12} md={6} lg={6} xl={6}>
                            <Card
                                hoverable
                                className={cs('animated', { 'rollIn': setAnimate })}
                            >
                                <div className="card-box">
                                    <div className="img-box">
                                        <img className="bg bg4" src={bg4}/>
                                    </div>
                                    <div className="mask animated fadeInUp">
                                        <div className="content">
                                            <div className="txt animated fadeInDown">
                                                <div className="tit">其他页面</div>
                                                <div className="detail">
                                                    运用echarts数据展示,工具类页面
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Card>
                        </Col>
                    </Row>
                </div>
            </div>
        )
    }
}

Project.defaultProps = {
    setAnimate: false
  };

  Project.propTypes = {
    setAnimate:PropTypes.bool,
}

export default Project;