import React, { Component } from 'react';
import Swiper from 'swiper/dist/js/swiper.js';
import 'swiper/dist/css/swiper.min.css';
import './home.less';

import Introduction from '../introduction/index';
import AboutMe from '../aboutme/index';
import Skill from '../skill/index';
import Experience from '../experience/index';
import Project from '../project/index';
import ContactMe from '../contact/index'
import Loading from '../loading/index'

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            aboutMeAnimate : false,
            skillAnimate:false,
            exAnimate:false,
            projectAnimate:false,
            contactAnimate:false,
            loading:true
        }
        this.aboutmeFirst = false;
        this.ifSillFirst = false;
        this.ifProjectFirst = false;
        this.ifEXFirst = false;
        this.ifContactirst = false;
    }
    componentDidMount(){
        this.initVerticalSwiper();
    }
    initVerticalSwiper(){
        let that = this;
        new Swiper('.mycv-swiper-container', {
            speed: 1000,
            direction: 'vertical',
            mousewheelControl: true,
            pagination: '.swiper-pagination',
            slidesPerView: 1,
            paginationClickable: true,
            // paginationBulletRender: function (swiper, index, className) {
            //     return '<span class="' + className + '">' + (index + 1) + '</span>';
            // },
            onInit:function(){
                setTimeout(() => {
                    that.setState({
                        loading: false
                    });
                }, 3600);
            },
            onSlideChangeStart:function (swiper) { 
                switch (swiper.activeIndex) {
                    case 1:
                        if(that.aboutmeFirst) return;
                        that.setState({
                            aboutMeAnimate: false
                        });
                        setTimeout(() => {
                            that.setState({
                                aboutMeAnimate: true
                            });
                            that.aboutmeFirst = true;
                        }, 500);
                        break;
                    case 2:
                        if(that.ifSillFirst) return;
                        that.setState({
                            skillAnimate: false
                        });
                        setTimeout(() => {
                            that.setState({
                                skillAnimate: true
                            });
                            that.ifSillFirst = true;
                        }, 500);
                        break;
                    case 3:
                        if(that.ifEXFirst) return;
                        that.setState({
                            exAnimate: false
                        });
                        setTimeout(() => {
                            that.setState({
                                exAnimate: true
                            });
                            that.ifEXFirst = true;
                        }, 500);
                        break;
                    case 4:
                        if(that.ifProjectFirst) return;
                        that.setState({
                            projectAnimate: false
                        });
                        setTimeout(() => {
                            that.setState({
                                projectAnimate: true
                            });
                            that.ifProjectFirst = true;
                        }, 500);
                        break;
                    case 5:
                            if(that.ifContactirst) return;
                            that.setState({
                                contactAnimate: false
                            });
                            setTimeout(() => {
                                that.setState({
                                    contactAnimate: true
                                });
                                that.ifContactirst = true;
                            }, 500);
                            break;
                    default:
                        break;
                }
            }
          });
    }
    render() {
        let {aboutMeAnimate,skillAnimate,exAnimate,projectAnimate,contactAnimate,loading} = this.state;
        return (
            <div className="mycv">
                <div className="mycv-swiper-container swiper-container">
                    <div className="swiper-wrapper">
                        <div className="swiper-slide"><Introduction/></div>
                        <div className="swiper-slide"> <AboutMe setAnimate={aboutMeAnimate}/>
                        </div>
                        <div className="swiper-slide"><Skill setAnimate={skillAnimate}/>
                        </div>
                        <div className="swiper-slide">
                        <Experience setAnimate={exAnimate}/>
                        </div>
                        <div className="swiper-slide">
                        <Project setAnimate={projectAnimate}/>
                        </div>
                        <div className="swiper-slide">
                            <ContactMe setAnimate={contactAnimate}></ContactMe>
                        </div>
                    </div>
                    <div className="swiper-pagination"></div>
                </div>
                <Loading ifloading={loading}/>
            </div>
        )
    }
}

export default Home;
