import React, { Component } from 'react';
import { CheckOutlined } from '@ant-design/icons';
import "./aboutme.less"
import mypic from "./images/my.jpg"
import cs from "classnames"
import PropTypes from 'prop-types';
import Parallax from 'parallax-js'

class AboutMe extends Component{

    constructor(props){
        super(props);

    }

    componentDidMount(){
        let scene = document.getElementById('aboutmescene');
        let parallaxInstance = new Parallax(scene);
    }



    render(){
        let {setAnimate} = this.props;

        return(
            <div className="aboutme">
                <div className="main-content" id="aboutmescene">
                    <div className="toux" data-depth="0.4">
                        <img src={mypic}/>
                    </div>
                    <div className="butterfly" data-depth="0.2"></div>
                    <div className={cs('arrow','animated', { 'fadeInDown': setAnimate, })}>

                    </div>
                    <div className={cs('me','animated', { 'fadeInLeft': setAnimate })}>Ii's Me</div>
                    <div className={cs('best','animated', { 'zoomIn': setAnimate })} >Strive to be the best !!</div>
                    <div className="content" data-depth="0.6">
                        <div className={cs('tag','name','animated', { 'swing': setAnimate })} >
                            姓名：Wendy
                        </div>
                        <div className={cs('tag','base','animated', { 'swing': setAnimate })}>
                            坐标：南通
                        </div>
                        <div className={cs('tag','calm','animated', { 'swing': setAnimate })}>
                            不浮躁
                        </div>
                        <div className={cs('tag','execute','animated', { 'swing': setAnimate })}>
                            执行力<CheckOutlined />
                        </div>
                        <div className={cs('tag','study','animated', { 'swing': setAnimate })}>
                            学习能力<CheckOutlined />
                        </div>
                        <div className={cs('tag','love','animated', { 'swing': setAnimate })}>
                            热爱前端
                        </div>
                        <div className={cs('tag','team','animated', { 'swing': setAnimate })}>
                            团队协作<CheckOutlined />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
AboutMe.defaultProps = {
    setAnimate: false
  };

AboutMe.propTypes = {
    setAnimate:PropTypes.bool,
}

export default AboutMe;